.debrief {
    text-align: left;
}

.debrief-header {
    margin-bottom: 2rem;
}

.debrief-header .header-copy {
    margin-top: 1rem;
    font-weight: bold;
}

.debrief-header hr {
    margin: 0;
}

.debrief-submit {
    text-align: center;
}




.debrief-submit .debrief-loading,
.debrief-submit .email-input,
.debrief-submit .mturkToken {
    width: 400px;
    margin: auto;
    border-radius: 5px;
    border: 1px solid #aaa;
    margin-bottom: 2rem;
}
.debrief-submit .mturkToken {
    background: #eee;
    font-size: 2rem;
    position: relative;
    width: 600px;
    padding: 20px 0;
}
.debrief-submit .email-input input {
    margin-top: 1rem;
}
.debrief-submit .debrief-loading .loadingSpinner {
    display: inline-block;
    padding: 0.5rem;
}

.debrief-submit .copy-to-clipboard {
    display: block;
    position: absolute;
    cursor: pointer;
    color: blue;
    bottom: 5px;
    right: 5px;
    font-size: 0.8rem;
}
.debrief-submit .copy-to-clipboard.copied {
    color: darkgrey;
    cursor: auto;
}