.App {
    height: 100vh;
    text-align: center;
}

.bold {
    font-weight: bold;
}
.underline {
    text-decoration: underline;
}
.italic {
    font-style: italic;
}
.centered {
    text-align: center;
}

.container {
    margin: auto;
    max-width: 900px;
}

.data-output {
    text-align: left;
    font-size: 0.8rem;
    background: #eee;
    padding: 1rem;
}

.vertical-center {
    display: flex;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
}

.form-check label {
    cursor: pointer;
}
input[type="radio"] {
    cursor: pointer;
}


.copyPage {
    font-size: 1.25rem;
    padding-bottom: 3rem;
    text-align: left;
}
.copyPage h2 {
    margin: 3rem 0;
}
.copyPage p {
    margin: 2rem 0;
}
.copyPage ol li {
    margin-bottom: 1rem;
}

button {
    padding: 10px 20px;
    border: 1px solid #999;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.2rem;
}

.navButtons {
    overflow: hidden;
}

.navButtons button {
    background: aliceblue;
}
.navButtons button:hover:enabled {
    background: #b6d6f3;
}

.navButtons .nextButton {
    float: right;
}
.navButtons .prevButton {
    float: left;
}








/*      RESULTS           
**************************/


.showResults {
    text-align: left;
    background: #eee;
}

.showResults pre {
    font-size: 0.7rem;
}











/*          IMAGE PRELOAD
*******************************/
#imagePreloader:after {
    content: "";
    background-image: url('/images/issues-bg.png');
    background-repeat: no-repeat;
    background-position: -9999px -9999px;
}

