#jspsych-experiment {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: #fff;
    font-size: 1rem;
}


/* instructions */
.blockIndex {
    text-align: right;
    position: absolute;
    top: 100px;
    right: 100px;
    font-size: 2rem;
}
.instructions {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.instructions .copy {
    max-width: 800px;
}
.instructions .copy p:first-child {
    font-size: 1.7rem;
    margin-bottom: 1.5rem;
    font-weight: 700;
    line-height: 2rem;
}

.instructions .protestor.icon {
    display: none;
}

@media screen and (min-width: 900px) {
    .instructions .protestor.icon {
        display: flex;
    }   
}


/* cue */
.cue .protestor.icon {
    transform: translateX(28px) translateY(92px);
}
.protest-sign-points {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    font-weight: 300;
}


/* protestor icon */
.icon.protestor {
    background-image: url('/images/protestor.png');
    background-size: 310px;
    background-position: -25px -30px;
    height: 360px;
    width: 320px;
    position: relative;
    background-repeat: no-repeat;
}

.protestor .protest-sign {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #666;
    position: absolute;
    height: 130px;
    width: 165px;
    top: 21px;
    left: 50px;
    font-size: 1.3rem;
    -webkit-transform: rotate(1deg);
    transform: rotate(1deg);

    font-weight: 700;
}

.protest-sign .protest-sign-position {
    font-weight: 700;
    display: block;
}





/* FEEDBACK */
.feedback {
    font-size: 2rem;
}

.feedback-message {
    font-weight: 700;
    margin-bottom: 2rem;
}




/* ASSESSMENT QUESTIONS */
.assessment-intro {
    max-width: 750px;
}

.assessment-question {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
    line-height: 2.5rem;
}