body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.radioRange ul {
    margin: 0;
    padding: 0;
}

.radioRange li {
    display: inline-block;
    position: relative;
    margin-right: 20px;
    cursor: pointer;
}

.QualityCheckSurvey h2 {
    margin-top: 2rem;
}

.QualityCheckSurvey .inner-wrapper {
    background: #f4f4f0;
    margin: 2rem auto;
    max-width: 800px;
    padding: 2rem;
}

.qcheck_3,
.qcheck_2,
.qcheck_1 {
    margin: 1rem 0 2rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #aaa;
    text-align: left;
}
.qcheck_1 .radioRange {
    width: 420px;
    text-align: center;
}
.qcheck_3 .radioRange {
    width: 660px;
    text-align: center;
}

.qcheck_3 .radioRange li,
.qcheck_1 .radioRange li {
    width: 55px;
    height: 40px;
}
.qcheck_3 .radioRange li {
    margin-right: 10px;
}

.qcheck_3 .form-legend,
.qcheck_1 .form-legend {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    width: 400px;
    font-size: 0.8rem;
    text-align: center;
    margin-bottom: 0.5rem;
}
.qcheck_3 .form-legend {
    width: 640px;
}

.qcheck_1 .radioRange li label,
.qcheck_3 .radioRange li label {
    position: absolute;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    align-items: center;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background: #fff;
    border: 1px solid #666;
    cursor: pointer;
}
.qcheck_1 .radioRange input:checked ~ label,
.qcheck_3 .radioRange input:checked ~ label {
    color: #fff;
    background: #007bff;
    border: 1px solid #043973;
}

.qcheck_2 textarea {
    display: block;
    width: 100%;
    height: 80px;
}






.qcheck_3 .qcheck_header {
    font-size: 1.2rem;
    width: 640px;
}

.qcheck_3a {
    margin-bottom: 2rem;
    border-bottom: 1px solid #ccc;
    padding-bottom: 1rem;
}





.qcheck_4 {
    text-align: left;
}
.qcheck_4 textarea {
    display: block;
    width: 100%;
    height: 150px;
}

.qcheck_email input {
    padding: 0.5rem 1rem;
    border-radius: 2px;
    border: 1px solid #aaa;
}
.intro2image1 img,
.intro3image1 img {
    width: 100%;
    height: auto;
}
 /* NUMBERED SCALE */
 #ms-selection-response-stimulus {
    font-weight: bold;
    font-size: 1.4rem;
    max-width: 720px;
}
 #ms-selection-response-stimulus p:first-child{
    font-weight: 400;
}
#ms-selection-response-prompt {
    margin-top: 3rem;
}
#ms-selection-response-prompt p {
    margin-top: 1rem;
}

.numbered-scale {
    position: relative;
}
.numbered-scale .grid-line {
    position: absolute;
    width: 400px;
    border-bottom: 1px solid #aaa;

    bottom: 10px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    z-index: 1;
}
.numbered-scale .items {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    width: 500px;
    margin: auto;
}
.numbered-scale .items span {
    display: block;
}
.numbered-scale .items div {
    width: 20%;
    z-index: 10;
}
.numbered-scale-tick {
    height: 25px;
    width: 25px;
    margin: auto;
    font-size: 8px;
}
.numbered-scale-tick.selected {
    background: #367ebd;
    border-radius: 50%;
    color: #367ebd
}

#jspsych-experiment {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background: #fff;
    font-size: 1rem;
}


/* instructions */
.blockIndex {
    text-align: right;
    position: absolute;
    top: 100px;
    right: 100px;
    font-size: 2rem;
}
.instructions {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}

.instructions .copy {
    max-width: 800px;
}
.instructions .copy p:first-child {
    font-size: 1.7rem;
    margin-bottom: 1.5rem;
    font-weight: 700;
    line-height: 2rem;
}

.instructions .protestor.icon {
    display: none;
}

@media screen and (min-width: 900px) {
    .instructions .protestor.icon {
        display: -webkit-flex;
        display: flex;
    }   
}


/* cue */
.cue .protestor.icon {
    -webkit-transform: translateX(28px) translateY(92px);
            transform: translateX(28px) translateY(92px);
}
.protest-sign-points {
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    font-weight: 300;
}


/* protestor icon */
.icon.protestor {
    background-image: url('/images/protestor.png');
    background-size: 310px;
    background-position: -25px -30px;
    height: 360px;
    width: 320px;
    position: relative;
    background-repeat: no-repeat;
}

.protestor .protest-sign {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #666;
    position: absolute;
    height: 130px;
    width: 165px;
    top: 21px;
    left: 50px;
    font-size: 1.3rem;
    -webkit-transform: rotate(1deg);
    transform: rotate(1deg);

    font-weight: 700;
}

.protest-sign .protest-sign-position {
    font-weight: 700;
    display: block;
}





/* FEEDBACK */
.feedback {
    font-size: 2rem;
}

.feedback-message {
    font-weight: 700;
    margin-bottom: 2rem;
}




/* ASSESSMENT QUESTIONS */
.assessment-intro {
    max-width: 750px;
}

.assessment-question {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
    line-height: 2.5rem;
}
.debrief {
    text-align: left;
}

.debrief-header {
    margin-bottom: 2rem;
}

.debrief-header .header-copy {
    margin-top: 1rem;
    font-weight: bold;
}

.debrief-header hr {
    margin: 0;
}

.debrief-submit {
    text-align: center;
}




.debrief-submit .debrief-loading,
.debrief-submit .email-input,
.debrief-submit .mturkToken {
    width: 400px;
    margin: auto;
    border-radius: 5px;
    border: 1px solid #aaa;
    margin-bottom: 2rem;
}
.debrief-submit .mturkToken {
    background: #eee;
    font-size: 2rem;
    position: relative;
    width: 600px;
    padding: 20px 0;
}
.debrief-submit .email-input input {
    margin-top: 1rem;
}
.debrief-submit .debrief-loading .loadingSpinner {
    display: inline-block;
    padding: 0.5rem;
}

.debrief-submit .copy-to-clipboard {
    display: block;
    position: absolute;
    cursor: pointer;
    color: blue;
    bottom: 5px;
    right: 5px;
    font-size: 0.8rem;
}
.debrief-submit .copy-to-clipboard.copied {
    color: darkgrey;
    cursor: auto;
}
.loadingSpinner {
    height: 80px;
    width: 80px;
}

.loadingSpinner img {
    width: 100%;
    height: auto;
}
.App {
    height: 100vh;
    text-align: center;
}

.bold {
    font-weight: bold;
}
.underline {
    text-decoration: underline;
}
.italic {
    font-style: italic;
}
.centered {
    text-align: center;
}

.container {
    margin: auto;
    max-width: 900px;
}

.data-output {
    text-align: left;
    font-size: 0.8rem;
    background: #eee;
    padding: 1rem;
}

.vertical-center {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.form-check label {
    cursor: pointer;
}
input[type="radio"] {
    cursor: pointer;
}


.copyPage {
    font-size: 1.25rem;
    padding-bottom: 3rem;
    text-align: left;
}
.copyPage h2 {
    margin: 3rem 0;
}
.copyPage p {
    margin: 2rem 0;
}
.copyPage ol li {
    margin-bottom: 1rem;
}

button {
    padding: 10px 20px;
    border: 1px solid #999;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1.2rem;
}

.navButtons {
    overflow: hidden;
}

.navButtons button {
    background: aliceblue;
}
.navButtons button:hover:enabled {
    background: #b6d6f3;
}

.navButtons .nextButton {
    float: right;
}
.navButtons .prevButton {
    float: left;
}








/*      RESULTS           
**************************/


.showResults {
    text-align: left;
    background: #eee;
}

.showResults pre {
    font-size: 0.7rem;
}











/*          IMAGE PRELOAD
*******************************/
#imagePreloader:after {
    content: "";
    background-image: url('/images/issues-bg.png');
    background-repeat: no-repeat;
    background-position: -9999px -9999px;
}

