 /* NUMBERED SCALE */
 #ms-selection-response-stimulus {
    font-weight: bold;
    font-size: 1.4rem;
    max-width: 720px;
}
 #ms-selection-response-stimulus p:first-child{
    font-weight: 400;
}
#ms-selection-response-prompt {
    margin-top: 3rem;
}
#ms-selection-response-prompt p {
    margin-top: 1rem;
}

.numbered-scale {
    position: relative;
}
.numbered-scale .grid-line {
    position: absolute;
    width: 400px;
    border-bottom: 1px solid #aaa;

    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
}
.numbered-scale .items {
    display: flex;
    align-items: flex-end;
    width: 500px;
    margin: auto;
}
.numbered-scale .items span {
    display: block;
}
.numbered-scale .items div {
    width: 20%;
    z-index: 10;
}
.numbered-scale-tick {
    height: 25px;
    width: 25px;
    margin: auto;
    font-size: 8px;
}
.numbered-scale-tick.selected {
    background: #367ebd;
    border-radius: 50%;
    color: #367ebd
}
