.QualityCheckSurvey h2 {
    margin-top: 2rem;
}

.QualityCheckSurvey .inner-wrapper {
    background: #f4f4f0;
    margin: 2rem auto;
    max-width: 800px;
    padding: 2rem;
}

.qcheck_3,
.qcheck_2,
.qcheck_1 {
    margin: 1rem 0 2rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #aaa;
    text-align: left;
}
.qcheck_1 .radioRange {
    width: 420px;
    text-align: center;
}
.qcheck_3 .radioRange {
    width: 660px;
    text-align: center;
}

.qcheck_3 .radioRange li,
.qcheck_1 .radioRange li {
    width: 55px;
    height: 40px;
}
.qcheck_3 .radioRange li {
    margin-right: 10px;
}

.qcheck_3 .form-legend,
.qcheck_1 .form-legend {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    width: 400px;
    font-size: 0.8rem;
    text-align: center;
    margin-bottom: 0.5rem;
}
.qcheck_3 .form-legend {
    width: 640px;
}

.qcheck_1 .radioRange li label,
.qcheck_3 .radioRange li label {
    position: absolute;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    align-items: center;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background: #fff;
    border: 1px solid #666;
    cursor: pointer;
}
.qcheck_1 .radioRange input:checked ~ label,
.qcheck_3 .radioRange input:checked ~ label {
    color: #fff;
    background: #007bff;
    border: 1px solid #043973;
}

.qcheck_2 textarea {
    display: block;
    width: 100%;
    height: 80px;
}






.qcheck_3 .qcheck_header {
    font-size: 1.2rem;
    width: 640px;
}

.qcheck_3a {
    margin-bottom: 2rem;
    border-bottom: 1px solid #ccc;
    padding-bottom: 1rem;
}





.qcheck_4 {
    text-align: left;
}
.qcheck_4 textarea {
    display: block;
    width: 100%;
    height: 150px;
}

.qcheck_email input {
    padding: 0.5rem 1rem;
    border-radius: 2px;
    border: 1px solid #aaa;
}